import * as React from 'react';
import { withStyles, WithStyles } from '@material-ui/core';
import Divider from '@material-ui/core/Divider';
import Paper from '@material-ui/core/Paper';
import Link from '@material-ui/core/Link';
import { Link as RouterLink } from 'react-router-dom';
import CopyrightIcon from '@material-ui/icons/Copyright';
import { connect } from 'react-redux';
import { otherMenuOptions } from '../../constants/resources';
import Style from './style';
import { RootState } from '../../reducers';

interface Props extends WithStyles<typeof Style> {
	kioskMode: boolean;
}
/**
 * Footer for founders section.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class Footer extends React.PureComponent<Props> {
	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes, kioskMode } = this.props;
		return kioskMode ? null : (
			<>
				<Paper square elevation={0} color="primary" className={classes.paper}>
					<div className={classes.root}>
						{otherMenuOptions.map((item, index) => (
							<>
								<Link
									key={item.name}
									rel="noopener noreferrer"
									to={item.link}
									color="inherit"
									component={RouterLink as any}
									className={classes.link}
								>
									{item.name}
								</Link>
								{index < otherMenuOptions.length - 1 && (
									<Divider orientation="vertical" color="default" flexItem />
								)}
							</>
						))}
					</div>
				</Paper>
				<div className={classes.copyright}>
					<CopyrightIcon fontSize="small" />
					{'  '}DMart 2021. All rights reserved.
				</div>
			</>
		);
	}
}

const mapStateToProps = (state: RootState) => ({
	kioskMode: state.feedbackReducer.kioskMode
});
export default withStyles(Style, { name: 'DmartCorpFreshFooter' })(
	connect(mapStateToProps)(Footer)
);
